import React from "react";
import styled from "styled-components";
// Assets
import MarketingIcon from "../../assets/svg/Services/MarketingIcon";
import SocialMediaIcon from "../../assets/svg/Services/SocialMediaIcon";
import EmailIcon from "../../assets/svg/Services/EmailIcon";
import WebDevIcon from "../../assets/svg/Services/WebDevIcon";

export default function ServiceBox({ icon, title, subtitle }) {
  let getIcon;

  switch (icon) {
    case "marketing":
      getIcon = <MarketingIcon />;
      break;
    case "socialMedia":
      getIcon = <SocialMediaIcon />;
      break;
    case "email":
      getIcon = <EmailIcon />;
      break;
    case "webDev":
      getIcon = <WebDevIcon />;
      break;
    default:
      getIcon = <MarketingIcon />;
      break;
  }

  return (
    <Wrapper className="flex flexColumn">
      <IconStyle>{getIcon}</IconStyle>
      <TitleStyle className="font20 extraBold">{title}</TitleStyle>
      <SubtitleStyle className="font14">{subtitle}</SubtitleStyle>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  width: 100%;
`;
const IconStyle = styled.div`
  @media (max-width: 860px) {
    margin: 0 auto;
  }
`;
const TitleStyle = styled.h2`
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
  padding: 40px 0;
  @media (max-width: 860px) {
    padding: 20px 0;
  }
`;
const SubtitleStyle = styled.p`
  width: 100%;
  max-width: 300px;
  text-align: justify;
  margin: 0 auto;
`;
