import React from "react";
import styled from "styled-components";
// Components
import ClientSlider from "../Elements/ClientSlider";
import ServiceBox from "../Elements/ServiceBox";

export default function Services() {
  return (
    <Wrapper id="services">
      <div className="whiteBg" style={{ padding: "60px 0" }}>
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">Our Awesome Services</h1>
            <p className="font14">
              We Provide Every Digital Service that You Can imagine.
              <br />
              Some of these are below.
            </p>
          </HeaderInfo>
          <ServiceBoxRow className="flex">
            <ServiceBoxWrapper>
              <ServiceBox
                icon="marketing"
                title="B2B Outreach Marketing"
                subtitle="Our targeted lead generation strategies play a vital role in a comprehensive marketing plan aimed at directly engaging your desired clients and enhancing your brand presence. While organic initiatives such as SEO and content tactics offer long-term advantages, it is crucial to achieve a professional return on investment (ROI). At our company, we have perfected a strategic blend of traditional lead generation methods and brand-building techniques that consistently yield successful results."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox
                icon="socialMedia"
                title="Social media marketing"
                subtitle="Leverage the power of prominent social media platforms, including Facebook, Twitter, Instagram, LinkedIn, Pinterest, Google, YouTube, Vimeo, and more, to boost brand visibility, drive website traffic, and capture attention. Our expert team will harness the full potential of these platforms to maximize your online presence and ensure your brand reaches its target audience effectively."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox
                icon="email"
                title="Email Marketing"
                subtitle="Revitalize your email list and go beyond just sending emails. Establish meaningful connections with your consumers and fully capitalize on their engagement. Email marketing remains the most efficient and profitable method for converting leads into loyal customers. Our expert team will help you unlock the true potential of your audience's participation, ensuring that each email campaign delivers maximum impact and drives tangible results for your business."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox
                icon="webDev"
                title="Web design & dev"
                subtitle="Our team comprises exceptionally skilled designers and developers who are at the forefront of their field. Their collective expertise enables us to deliver outstanding websites that seamlessly blend elegance with functionality. We prioritize a collaborative approach, working closely with our clients to understand their unique company goals and translate them into tangible online solutions. With our team by your side, you can expect nothing less than a website that not only meets but exceeds your expectations." />
            </ServiceBoxWrapper>
          </ServiceBoxRow>
        </div>
      </div>
      <div className="lightBg" style={{ padding: "50px 0" }}>
        <div className="container">
          <ClientSlider />
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
`;
const ServiceBoxRow = styled.div`
  @media (max-width: 860px) {
    flex-direction: column;
  }
`;
const ServiceBoxWrapper = styled.div`
  width: 20%;
  margin-right: 5%;
  padding: 80px 0;
  @media (max-width: 860px) {
    width: 100%;
    text-align: center;
    padding: 40px 0;
  }
`;
const HeaderInfo = styled.div`
  @media (max-width: 860px) {
    text-align: center;
  }
`;